<template>
  <div class="channel-modal">
    <a-modal
      :title="title"
      :visible="channelVisible"
      @cancel="handleCancel"
      cancelText="取消"
      okText="确定"
      :mask="false"
      :maskClosable="false"
      :width="600"
      class="channelCls"
    >
      <a-form-model ref="channelForm" :model="channelForm" :rules="channelFormRule">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="频道名称" prop="cname">
              <a-input v-model="channelForm.cname" placeholder="请输入频道名称" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="频道别名" prop="ename">
              <a-input v-model="channelForm.ename" placeholder="请输入频道唯一别名" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="分析对象" prop="objectId">
              <a-select v-model="channelForm.objectId" placeholder="请选择分析对象" allowClear>
                <a-select-option
                        v-for="objectName in objectData"
                        :key="objectName.id"
                        :value="objectName.id"
                        :disabled = "objectName.isBind === 1 && objectName.id !== currentData.objectId"
                >{{objectName.objectName}}
                  <span v-if="objectName.isBind === 1 && objectName.id !== currentData.objectId">（已绑定）</span>
                  <span v-if="objectName.isBind === 1 && objectName.id === currentData.objectId">（当前绑定）</span>
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="频道描述" prop="description">
              <a-textarea v-model="channelForm.description" placeholder="请输入频道描述信息" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel">取消</a-button>
          <a-button type="primary" @click="handleOk">提交</a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: "channel-modal",
  props: {
    // 模态框的显示与隐藏
      channelVisible: {
        type: Boolean,
        default: false,
        required: true,
      },
    // 模态框的标题
    title: {
      type: String,
      default: "添加/编辑频道",
    },
    // 表单内容 一般添加的时候才会传入
    content: {
      type: Object,
      default: function () {
        return {
          id:"",
          cname: "",
          ename: "",
          description: "",
          creator: "",
          createTime: "",
          updateTime: "",
        };
      },
    },
    // 关于操作类型 0为添加 1为编辑
    optType:{
      type:Number,
      default:0,
      required: true,
    },
  },
  data() {
    return {
        channelFormRule: {
          cname: [{ required: true, message: "请输入频道名称" }],
          ename: [{ required: true, message: "请输入频道别名" }],
          objectId: [{ required: true, message: "请选择分析对象" }],
        },
        channelForm:{
          cname: "",
          ename: "",
          description: "",
          objectId:""
        },
        // 分析对象列表
        objectNameData:[],
        currentData:{}
    };
  },
  computed:{
    ...mapState([
                'objectData'
    ])
  },
  methods: {
    handleOk() {
        this.$refs.channelForm.validate(valid => {
            if(valid){
                this.$emit("channelOk", {formContent:this.channelForm,optType:this.optType});
            } else {
                this.$message.warning('输入有误，请检查');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("channelCancel", "我点击取消");
    },
      // 获取分析对象列表
      // getObject() {
      //     let params = {};
      //     this.$API.searchObject(params).then((res) => {
      //         if (res.code === 1000) {
      //             this.objectNameData = res.data;
      //         } else {
      //             this.objectNameData = [];
      //         }
      //     });
      // },
  },
  watch:{
    content:{
      handler:function(newVal){
        this.currentData = Object.assign({},newVal)
        Object.assign(this.channelForm,newVal);
      },
      deep:true
    }
  },
    created(){
      // this.getObject()
    }
};
</script>
<style>
.channelCls .ant-modal-content,.channelCls .ant-modal-header {
    background-color: #054273;
    color: #fff;
}
.channelCls .ant-modal-header {
    border-bottom: 1px solid #1890ff

}
.channelCls .ant-modal-header .ant-modal-title{
    color: #fff;
}
.channelCls .ant-modal-footer{
     border-top: 1px solid #1890ff
}
.channelCls .ant-modal-close{
     color: inherit;
}
</style>