<template>
    <div class="config pageSpace h100">
        <my-breadcrumb></my-breadcrumb>
        <div class="pageContent">
            <a-layout-content>
                <div class="channel">
                    <div class="tableTopTools clearfix">
                        <div class="buttons fl">
                            <p class="toolName fl">工具栏：</p>
                            <a-button type="primary" @click="optchannel">
                                <a-icon type="plus"></a-icon>
                                添加频道
                            </a-button>
                        </div>
                    </div>
                    <div class="tableContent">
                        <a-table :columns="columns" :dataSource="channelData" rowKey="id" :pagination="false">
                         <span slot="objectId" slot-scope="text,record">
                           <span>{{record.objectId | getObjectCnById(that)}}</span>
                          </span>
                         <span slot="gmtCreate" slot-scope="text,record">
                           <span>{{record.gmtCreate | dateFormatter(1)}}</span>
                          </span>
                            <span slot="gmtModified" slot-scope="text,record">
                              <span>{{record.gmtModified | dateFormatter(1)}}</span>
                            </span>
                            <span slot="action" slot-scope="text,record">
                               <a class="t-optColor-normal" @click="editchannel(record)">编辑</a>
                                <a class="t-optColor-warning" @click="deletechannel(record)">删除</a>
                                <a class="t-optColor-success" @click="openField(text,record)">详情</a>
                                <a class="t-optColor-success" @click="cloneChannel(text,record)">克隆</a>
                           </span>
                        </a-table>
                        <div class="ar" style="margin-top:30px" v-if="channelData.length > 0">
                            <a-pagination
                                    v-model="pageParam.page"
                                    :total="total"
                                    :page-size="pageParam.size"
                                    @change="pageChange"
                            ></a-pagination>
                        </div>
                    </div>
                    <channelModal
                            :channelVisible="channelVisible"
                            @channelOk="channelOk"
                            @channelCancel="channelCancel"
                            :content="content"
                            :optType="optType"
                    ></channelModal>
                    <a-modal
                        title="克隆字段信息"
                        :visible="cloneVisible"
                        @cancel="cloneCancel"
                        cancelText="取消"
                        okText="确定"
                        :mask="false"
                        :width="600"
                        class="modalCls"
                    >
                        <a-form-model ref="cloneForm" :model="cloneForm" :rules="cloneFormRule">
                            <a-row :gutter="[24,24]" type="flex" justify="center">
                                <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
                                    <span style="color:#ccc;">要克隆频道：</span><span style="color:#faad14">{{cloneData.cname}}</span>
                                </a-col>
                            </a-row>
                            <a-row :gutter="[24,24]" type="flex" justify="center">
                                <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
                                    <a-form-model-item label="新频道名称" prop="cname">
                                        <a-input v-model="cloneForm.cname" placeholder="请输入要克隆的新频道名称" />
                                    </a-form-model-item>
                                </a-col>
                            </a-row>
                            <a-row :gutter="[24,24]" type="flex" justify="center">
                                <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
                                    <a-form-model-item label="新频道别名" prop="ename">
                                        <a-input v-model="cloneForm.ename" placeholder="请输入要克隆的新频道唯一别名" />
                                    </a-form-model-item>
                                </a-col>
                            </a-row>
                            <a-row :gutter="[24,24]" type="flex" justify="center">
                                <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
                                    <a-form-model-item label="新频道所属分析对象" prop="objectId">
                                    <a-select v-model="cloneForm.objectId" placeholder="请为新频道选择分析对象" allowClear>
                                        <!-- <a-select-option
                                                v-for="objectName in objectNameData"
                                                :key="objectName.id"
                                                :value="objectName.id"
                                        >{{objectName.objectName}}</a-select-option> -->
                                        <a-select-option
                                                v-for="objectName in objectNameData"
                                                :key="objectName.id"
                                                :value="objectName.id"
                                                :disabled = "objectName.isBind === 1"
                                            >{{objectName.objectName}}
                                            <span v-if="objectName.isBind === 1">（已绑定）</span>
                                        </a-select-option>
                                    </a-select>
                                    </a-form-model-item>
                                </a-col>
                            </a-row>
                            <a-row :gutter="[24,24]" type="flex" justify="center">
                                <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
                                    <a-form-model-item label="新频道描述" prop="description">
                                    <a-textarea v-model="cloneForm.description" placeholder="请输入新频道描述信息" rows="4"></a-textarea>
                                    </a-form-model-item>
                                </a-col>
                            </a-row>
                        </a-form-model>
                        <template slot="footer">
                            <a-button type="link" ghost @click="cloneCancel">取消</a-button>
                            <a-button type="primary" @click="cloneOk">提交</a-button>
                        </template>
                    </a-modal>
                </div>
            </a-layout-content>
        </div>
    </div>
</template>
<script>
    import channelModal from "@/components/myModal/channel-modal.vue";
    import { mapState } from 'vuex'
    export default {
        name: "project-list",
        components: {channelModal},
        computed:{
            ...mapState([
                'objectData'
            ])
        },
        data() {
            return {
                // 表格列
                columns: [
                    {
                        dataIndex: "cname",
                        title: "频道名称",
                    },
                    {
                        dataIndex: "objectId",
                        title: "分析对象",
                        scopedSlots: { customRender: "objectId" },
                    },
                    {
                        dataIndex: "ename",
                        title: "频道英文名",
                    },
                    {
                        dataIndex: "description",
                        title: "频道描述",
                    },
                    {
                        dataIndex: "creator",
                        title: "创建者",
                    },
                    {
                        dataIndex: "gmtCreate",
                        title: "创建时间",
                        scopedSlots: { customRender: "gmtCreate" },
                    },
                    {
                        dataIndex: "gmtModified",
                        title: "最近更新时间",
                        scopedSlots: { customRender: "gmtModified" },
                    },
                    {
                        dataIndex: "action",
                        title: "操作",
                        scopedSlots: { customRender: "action" },
                    },
                ],
                // 表格数据
                channelData: [],
                // 操作模态框的显示 隐藏
                channelVisible: false,
                // 操作模态框的内容
                content: {
                    id: null,
                    objectId: null,
                    cname: "",
                    ename: "",
                    description: "",
                    creator: "",
                    gmtCreate: "",
                    gmtModified: "",
                },
                // 模态框的操作类型 0添加 1编辑
                optType: 0,
                pageParam: {
                    page: 1,
                    size: 20,
                },
                searchInput: "",
                total: 0,
                // 实例对象
                that:this,
                cloneVisible:false,
                cloneForm:{
                    cname:"",
                    ename:"",
                    description: "",
                    creator: "",
                    createTime: "",
                    updateTime: "",
                },
                cloneFormRule:{
                    cname: [{ required: true, message: "请输入频道名称" }],
                    ename: [{ required: true, message: "请输入频道别名" }],
                    objectId: [{ required: true, message: "请选择分析对象" }],
                },
                cloneData:{},
                // 分析对象列表
                objectNameData:[]
            };
        },
        created() {
            this.getchannel();
            this.getObjectData();
            // 获取分析对象列表
            this.getObject();
        },
        watch:{
            objectData:{
                handler:function(newVal){
                    this.objectNameData = newVal;
                    // console.log(this.objectNameData)
                    // 分析对象数据本地缓存发生变化后，请求频道数据
                    this.getchannel();
                },
                deep:true
            }
        },
        methods: {
            //  查询频道
            getchannel() {
                this.$API.searchChannel(this.pageParam).then((res) => {
                    if (res.code === 1000) {
                        this.channelData = res.data;
                        this.total = res.total;
                    } else {
                        this.channelData = [];
                    }
                });
            },
            getObject() {
                let params = {};
                this.$API.searchObject(params).then((res) => {
                    if (res.code === 1000) {
                        this.objectNameData = res.data;
                    } else {
                        this.objectNameData = [];
                    }
                });
            },
            optchannel() {
                this.resetContent();
                this.channelVisible = true;
                this.optType = 0;
            },
            //   编辑
            editchannel(column) {
                this.content = column;
                this.optType = 1;
                this.channelVisible = true;
            },
            // 删除
            deletechannel(column) {
                const that = this;
                this.$confirm({
                    title: "确定删除该条数据吗？",
                    content: () => <div style="color:red">删除后将不可恢复</div>,
                    okText: "确认删除",
                    cancelText: "取消删除",
                    onOk() {
                        that.$API.delChannel(column.id).then((res) => {
                            if (res.code === 1000) {
                                that.$message.success("删除成功");
                                // 更新缓存的分析对象数据
                                that.getObjectData();
                            } else {
                                that.$message.warning(res.message);
                            }
                        });
                    },
                });
            },
            // 查看详情
            openField(text, column) {
                this.curRow = column;
                this.$router.push({
                    name: `field`,
                    query: {
                        id: column.id,
                        cname: column.cname
                    }
                });
            },
            // 添加channel
            channelOk(val) {
                if (val.optType) {
                    let param = Object.assign({}, val.formContent, {id: this.content.id});
                    this.$API.editChannel(param).then((res) => {
                        if (res.code === 1000) {
                            console.log(3333)
                            this.$message.success("频道更新成功");
                            this.channelVisible = false;
                            // 更新缓存的分析对象数据
                            this.getObjectData();
                            // this.getchannel();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                } else {
                    this.$API.addChannel(val.formContent).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("添加频道成功");
                            this.channelVisible = false;
                            // 更新缓存的分析对象数据
                            this.getObjectData();
                            // this.getchannel();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                }

            },
            // 取消channel
            channelCancel() {
                this.channelVisible = false;
                this.resetContent();
            },
            pageChange() {
                this.getchannel();
            },
            // 搜索关键词变化事件
            keywordChange() {
                this.pageParam.page = 1;
                this.getchannel();
            },
            // 克隆当前频道
            cloneChannel(text,row){
                this.cloneData = row;
                this.cloneVisible = true;
            },
            cloneCancel(){
                this.cloneVisible = false;
            },
            cloneOk(){
                this.$refs.cloneForm.validate(valid => {
                    if(valid){
                        let param = Object.assign({},this.cloneForm,{channelId:this.cloneData.id});
                        this.$API.cloneChannel(param).then(res => {
                            if(res.code === 1000){
                                this.$message.success(res.message);
                                // 更新缓存的分析对象数据
                                this.getObjectData();
                            } else {
                                this.$message.warning(res.message);
                            }
                        })
                        this.cloneVisible = false;
                    } else {
                        this.$message.warning('输入有误，请检查');
                        return false
                    }
                })
            },
            // 清洗content
            resetContent() {
                this.content = {
                    id: "",
                    objectId: "",
                    cname: "",
                    ename: "",
                    description: "",
                    creator: "",
                    gmtCreate: "",
                    gmtModified: "",
                };
            },
            // 获取分析对象数据
            getObjectData(){
                console.log(111)
                this.$store.dispatch('getObjectData',this);
            }
        },
    };
</script>
<style>
    .searchInput {
        width: 300px;
    }
</style>